<template>
  <div class="employBox">
    <div class="headBox">
      <div class="selectClass">
        <el-select v-model="selectClass" placeholder="请选择查看的班级" @change="changeClass">
          <el-option
            v-for="item in classList"
            :key="item.classNo"
            :label="item.className"
            :value="item.classNo"
          ></el-option>
        </el-select>
      </div>
      <div class="selectChart">
        <el-switch v-model="isLineShow" active-text="查看折线图" inactive-text="查看饼状/云图"></el-switch>
      </div>
    </div>
    <div class="dataChart">
      <div class="lineBox" v-if="isLineShow">
        <lineChart :lineData="lineData"></lineChart>
      </div>
      <div class="pieCloudBox" v-else>
        <div class="pieBox">
          <pieChart :chartData="chartData"></pieChart>
        </div>
        <div class="clouldBox">
          <word-cloud-chart id="echarts05" :data="jobNameData" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  interClassInfo,
  interStatistInfo
} from "../../../../../utils/employment.url";
import lineChart from "@/components/dataChart/lineChart.vue";
import pieChart from "@/components/dataChart/pieChart.vue";
import WordCloudChart from "@/components/dataChart/WordCloudChart.vue";
export default {
  components: { lineChart, pieChart, WordCloudChart },
  data() {
    return {
      userInfo: {},
      classList: [],
      statistList: [],
      classStatistList: [],
      selectClass: "",
      isLineShow: false, //是否展示折线图
      //云图数据
      jobNameData: [],
      //饼状数据
      chartData: {
        columns: ["isEmploy", "number"],
        rows: [
          { isEmploy: "已就业", number: 0 },
          { isEmploy: "未就业", number: 0 }
        ]
      },
      //折线图数据
      lineData: {
        classData: [],
        allData: [],
        timeData: []
      }
    };
  },
  methods: {
    //获取班级列表信息
    getClassInfo() {
      interClassInfo({ counselorNo: this.userInfo.userId }, "get")
        .then(res => {
          if (res.status == 200) {
            this.classList = res.data;
            //初始化饼状就业数据
            this.selectClass = this.classList[0].classNo;
            this.initializeStatist(this.selectClass);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //获取就业统计
    getStatistInfo(classNo) {
      interStatistInfo({ classNo: classNo ? classNo : "" }, "get")
        .then(res => {
          if (res.status == 200 && classNo) {
            this.classStatistList = res.data;
            //格式化时间戳
            this.classStatistList.map((item, index) => {
              item.contractTime = item.contractTime.slice(0, 7);
            });
            //对班级就业信息进行签约时间分组
            var lineData = this.groupBy(this.classStatistList, function(item) {
              return [item.contractTime]; //按照时间年月分组
            });
            // console.log(lineData)
            var dateToTime = function(str) {
              return new Date(str.replace(/-/g, "/")).getTime(); //用/替换日期中的-是为了解决Safari的兼容
            };
            for (var i = 0; i < lineData.length; i++) {
              lineData[i].publishTimeNew = dateToTime(
                lineData[i][0].contractTime
              );
            }
            lineData.sort(function(a, b) {
              return b.publishTimeNew > a.publishTimeNew ? -1 : 1;
            });
            // console.log(lineData)
            //获取学院年级就业时间线
            var timeLine = this.lineData.timeData;
            var countClassTiem = 0;
            // console.log(timeLine);
            timeLine.map((item, index) => {
              // console.log(item, lineData[countClassTiem][0].contractTime);
              // console.log("[]][][][][]]");
              if (lineData[countClassTiem] && item == lineData[countClassTiem][0].contractTime) {
                console.log(item);
                // console.log("11111");
                if (index == 0) {
                  this.lineData.classData.push(lineData[countClassTiem].length); //时间段内的就业人数
                } else {
                  this.lineData.classData.push(
                    lineData[countClassTiem].length + this.lineData.classData[index - 1]
                  ); //时间段内的就业人数
                }
                countClassTiem++;
              } else {
                // console.log("000000", this.lineData.classData[index - 1]);
                if (index == 0) {
                  this.lineData.classData.push(0); //时间段内的就业人数
                } else {
                  this.lineData.classData.push(
                    this.lineData.classData[index - 1]
                  ); //时间段内的就业人数
                }
              }
              // console.log(this.lineData.classData)
            });

            // lineData.map((item, index) => {
            //   console.log(item[0].contractTime,timeLine[index])
            //   console.log("[]][][][][]]")
            //   if(item[0].contractTime == timeLine[index]){
            //     console.log("11111")
            //     if (index == 0) {
            //     this.lineData.classData.push(item.length); //时间段内的就业人数
            //   }else{
            //     this.lineData.classData.push(item.length + this.lineData.classData[index-1]); //时间段内的就业人数
            //   }
            //   }else{
            //     console.log("000000")
            //     if (index == 0) {
            //     this.lineData.classData.push(0); //时间段内的就业人数
            //   }else{
            //     this.lineData.classData.push(this.lineData.classData[index-1]); //时间段内的就业人数
            //   }
            //   }
            // });

            // console.log("========")
            // console.log(this.lineData.classData);
            // console.log("++++++++++++");
            // console.log(this.lineData.classData);
            //云图数据处理
            var wordCloudData = this.groupBy(this.classStatistList, function(
              item
            ) {
              return [item.jobName];
            });
            wordCloudData.map((item, index) => {
              this.jobNameData.push({
                name: item[0].jobName,
                value: item.length
              });
              // console.log(this.jobNameData);
            });
          } else if (res.status == 200 && !classNo) {
            this.statistList = res.data;
            //格式化时间戳
            this.statistList.map((item, index) => {
              item.contractTime = item.contractTime.slice(0, 7);
            });
            //对年级就业信息进行签约时间分组
            var lineData = this.groupBy(this.statistList, function(item) {
              return [item.contractTime]; //按照时间年月分组
            });
            var dateToTime = function(str) {
              return new Date(str.replace(/-/g, "/")).getTime(); //用/替换日期中的-是为了解决Safari的兼容
            };
            for (var i = 0; i < lineData.length; i++) {
              lineData[i].publishTimeNew = dateToTime(
                lineData[i][0].contractTime
              );
            }
            lineData.sort(function(a, b) {
              return b.publishTimeNew < a.publishTimeNew ? 1 : -1;
            });
            lineData.map((item, index) => {
              this.lineData.timeData.push(item[0].contractTime);
              if (index == 0) {
                this.lineData.allData.push(item.length); //时间段内的就业人数
              } else {
                this.lineData.allData.push(
                  item.length + this.lineData.allData[index - 1]
                ); //时间段内的就业人数
              }
              // this.lineData.allData.push(item.length); //时间段内的就业人数
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    changeClass() {
      this.lineData.classData = [];
      this.jobNameData = [];
      this.initializeStatist(this.selectClass);
    },
    // 初始化就业就业统计的饼状图云图以及折线图
    initializeStatist(classNo) {
      this.classList.map((item, index) => {
        if (item.classNo == classNo) {
          this.chartData.rows[0].number = item.employNum;
          this.chartData.rows[1].number = item.classNum - item.employNum;
        }
      });
      //获取班级就业数据
      this.getStatistInfo(classNo);
    },
    //数组对象分组
    groupBy(array, f) {
      var groups = {};
      array.forEach(function(o) {
        var group = JSON.stringify(f(o));
        groups[group] = groups[group] || [];
        groups[group].push(o);
      });
      return Object.keys(groups).map(function(group) {
        return groups[group];
      });
    }
  },
  created() {
    //饼状就业数据初始化后续注释删除
    // this.selectClass = this.classList[0].classNo;
    // this.initializeStatist(this.selectClass);

    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getClassInfo();
    //获取年级就业信息
    this.getStatistInfo();
  }
};
</script>
<style lang="scss" scoped>
.employBox {
  .headBox {
    margin: 10px 50px;
    .selectClass,
    .selectChart {
      display: inline-block;
    }
    .selectChart {
      float: right;
    }
  }
  .dataChart .pieCloudBox {
    .pieBox,
    .clouldBox {
      display: inline-block;
      vertical-align: top;
      //   width: 50%;
    }
    .pieBox {
      width: 50%;
    }
    .clouldBox {
      width: 50%;
      background: #ffffff00;
    }
  }
}
</style>